.hero {
	position: relative;
	width: 100vw;
	height: 900px;
	overflow: hidden;
	margin-top: -63px;

	&__bg {
		z-index: -1;
		width: 100vw;
		position: absolute;
		inset: 0;

		& img {
			position: absolute;
			inset: 0;
			width: 100%;
			height: 900px;
			object-fit: cover;
		}
	}

	&__title {
		font-family: 'DIN Pro medium';
		font-weight: 500;
		color: #E7B656;
		font-size: 96px;
		line-height: 87px;
		text-transform: uppercase;
		letter-spacing: 10px;
		margin: 50px 0;
	}

	&__subtitle {
		position: relative;
		font-size: 40px;
		line-height: 45px;
		display: inline-block;
		padding-top: 28px;
		
		color: #FFFFFF;
		&::before {
			display: block;
			position: absolute;
			content: "";
			top: 0;
			left: 20px;
			width: 90%;
			height: 1px;
			background-color: #FFFFFF;
		}
	}

	&__content {
		height: 900px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	&__row {
		display: flex;
		justify-content: center;
		width: 100%;
		gap: 540px;
	}
	
	&__control {
		display: flex;
		gap: 32px;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		color: #FFFFFF;
		font-size: 28px;
		line-height: 33px;
		letter-spacing: -2px;
		transition: opacity .3s;

		&:hover {
			opacity: .7;
		}

		&--left {
			align-items: flex-end;
		}

		&--right {
			color: #E7B656;
		}
	}

	&__arrow {
		height: 32px;
		width: 70px;

		&--right {
			transform: scaleX(-1);
		}
	}
}