.header {	
	background-color: transparent;
	position: relative;
	z-index: 1;
	height: 63px;

	&__wrapper {
		position: relative;
		padding-top: 53px;
		width: 100%;
		display: flex;
		justify-content: space-between;
	}

	&__burger {
		align-self: flex-start;
		display: block;
		width: 72px;
		height: 31px;
		flex-shrink: 0;
		background-color: transparent;
		color: #fff;
		padding: 0;
		border: none;
		position: relative;
		z-index: 3;
		cursor: pointer;
		transition: opacity .3s;

		& i {
			display: block;
			height: 3px;
			width: 72px;
			border-radius: 2px;
			background: currentColor;
			left: 0;
			top: 0;
			margin-bottom: 11px;
		}

		&:hover {
			opacity: .7;
		}
	}

	&__langs {
		position: absolute;
		bottom: 20px;
		left: calc(50% - 57px);

		& a {
			color: #fff;
			font-size: 28px;
			line-height: 33px;
			transition: color .3s;
		}

		& a.current {
			color: #E7B656;
		}

		& a:hover {
			color: #E7B656;
		}
	}

	&__contact-us {
		flex-shrink: 0;
		align-self: flex-start;
		color: #fff;
		font-size: 24px;
		line-height: 35px;
		background-color: rgba(255, 255, 255, .2);
		padding: 10px 20px;
		border-radius: 15px 0 15px 15px;
		cursor: pointer;
		transition: background-color .3s;

		&:hover {
			background-color: #E7B656;
		}
	}
}