.request {
	background-color: #003053;
	padding: 60px 0;

	&__row {
		display: flex;
	}

	&__contacts {
		display: flex;
		gap: 36px;
		justify-content: flex-end;
		transition: color .3s;

		& a {
			color: #E7B656;

			&:hover {
				color: darken($color: #E7B656, $amount: 15%);
			}
		}
	}
}