.partners {
	padding: 100px 0;

	&__text {
		display: flex;
		align-items: center;
		gap: 20px;

		& span:first-child {
			display: flex;
			font-size: 325px;
			line-height: 296px;
			letter-spacing: -2px;
			background: linear-gradient(to bottom, #E7B656, #816630);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
}

/* Скролл картинок */
.scrolled {
	// width: 100vw;
	margin-top: -55px;
}

.scrolled__block {
	position: relative;
	overflow: hidden;
}

.scrolled__content {
	display: flex;
	width: 100%;
	gap: 25px;
	justify-content: center;
	// margin: 0 auto;
}

.scrolled__item {
	flex-shrink: 0;
	width: 362px;
	height: 168px;
	padding: 40px 45px;
	border: 1px solid #fff;
	background-color: rgba($color: #ffffff, $alpha: .03);
	backdrop-filter: blur(10px);
	border-radius: 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: opacity .3s;
	opacity: .5;

	&.active {
		opacity: 1;
	}

	& img {
		object-fit: contain;
	}

}
.scrolled__line {
	position: relative;
	display: flex;
	flex-direction: row;
	gap: 30px;
	width: 100vw;
}

.scrolled__line-content {
	display: flex;
	gap: 25px;
	flex-direction: row;
	width: 100%;
	animation: scroll 30s infinite linear;
}

@keyframes scroll {
	0% {
		transform: translateX(0%);
	}

	100% {
		transform: translateX(-100%);
	}
}