.section-head {
	display: flex;
	align-items: center;
	
	&__title {
		position: relative;
		color: #E7B656;
		font-size: 128px;
		line-height: 115px;
		padding-right: 30px;

		&::after {
			display: block;
			position: absolute;
			content: '';
			right: 0;
			top: 10px;
			height: 90%;
			width: 1px;
			background-color: #E7B656;
		}
	}

	&__text {
		padding-left: 30px;
		font-weight: 500;
		font-size: 32px;
		line-height: 37px;
	}
}